$max-width: 900px;

$sans-serif-stack: "Libre Franklin", "Franklin Gothic", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
$serif-stack: "IM Fell French Canon", "Georgia", "Times New Roman", serif;

$link-underline-thickness: 2px;
$link-color: #1d57d5;
$link-visited-color: #042e88;
$link-underline-color: #bdbdbd;
$link-hover-color: #5d89e9;
$link-after-color: #598eff;

$bolder-background-color: #dbe6ff;
$opaque-center-background-color: #f2f7f4;

html {
    height: 100%;
    
    background-color: $bolder-background-color;
    scrollbar-gutter: stable both-edges; //center background even if scrollbar is visible
    
    background-image: url("/assets/images/background_pattern.svg");
    background-size: 150px;
    background-position: top center;
    background-repeat: repeat;
}

body {
    margin: auto;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-bottom: 0px;
    max-width: $max-width;
    padding-left: 50px;
    padding-right: 50px;
    min-height: 100%;
    box-sizing: border-box;

    background-color: $opaque-center-background-color;
    font-family: $serif-stack;
}

p {
    font-size: 1.1em; // IM Fell font is a little smaller than average
    margin-top: 0px;
}

h1, h2, h3, h4, h5, h6 {
    font-family: $sans-serif-stack;
}

a {
    text-decoration-color: $link-underline-color;
    text-decoration-thickness: $link-underline-thickness;
    color: $link-color;
}

a:visited, a:active {
    color: $link-visited-color;
}

a:hover, .homelink:hover {
    color: $link-hover-color;
}

.footer {
    font-size: .9em;
}

hr {
    border-color: #000000aa;
}

h1 {
    margin-top: 15px;
    margin-bottom: 20px;
}

h2, h3, h4, h5, h6 {
    margin-top: 15px;
    margin-bottom: 5px;
}


// If there is not space for the max-width margins, make the padding way less
@media screen and (width <= $max-width) {
    body {
        padding-left:  10px;
        padding-right: 10px;
    }
}

// Make background match center background if small enough that the scrollbar
// gutter would be visible
@media screen and (width <= calc($max-width + 30px)) {
    html {
        background-color: $opaque-center-background-color;
    }
}

.libre-franklin {
    font-family: $sans-serif-stack;
}